import React, {Component} from "react";
import ParallaxImageItem from "./ParallaxImageItem";
import ParallaxVideoItem from "./ParallaxVideoItem";
import {baseUrl, PARALLAX_ARRAY} from "../../config.constants";
import {fetchData} from "../../fetch.service";

export default class ParallaxLayout extends Component {

	constructor(props) {
		super(props);
		this.state = {
			items: []
		};
	}

	componentWillMount() {
		fetchData.getRandom(3).then(res => {
			if (res.status === 200 && res.data) {
				this.setState({
					items: res.data
				});
			}
		});
	}

	renderParallax() {
		if (this.state.items.length > 0) {
			return "<ul>" +
					this.state.items.map((item, idx) => {
						return (
								<ParallaxImageItem
										title={""}
										key={idx}
										description={""}
										src={baseUrl + item.toString().replace("/\\/g", "/")}/>);
					})
					+ "</ul>";
		}
	}

	render() {
		return (
				<div className="tp-fullscreen-container revolution">
					<div className="tp-fullscreen">
						{
							this.renderParallax()
						}
						{false && PARALLAX_ARRAY.map((item, idx) => {
							if (item.isVideo) {
								return (
										<ParallaxVideoItem
												title={item.title}
												key={idx}
												description={item.description}
												src={item.image_src}
												videosrc={item.video_src}/>
								);
							} else {
								return (
										<ParallaxImageItem
												title={item.title}
												key={idx}
												description={item.description}
												src={item.image_src}/>);

							}
						})}

						<div className="tp-bannertimer tp-bottom"/>
					</div>
				</div>
		)
	}

}



